





















import { defineComponent, ref } from "@vue/composition-api";

export default defineComponent({
  emits: {
    save: (text: string) => text.length > 1,
  },
  setup(_props, { emit }) {
    const dialog = ref(false);
    const text = ref("");

    const close = () => {
      dialog.value = false;
      text.value = "";
    };

    const save = () => {
      emit("save", text.value);
      close();
    };

    return {
      dialog,
      text,
      close,
      save,
    };
  },
});
